
/**
 * SequentialGPU Build Configuration (Generated at build time)
 */

export const BUILD_TARGETS = {
    PRODUCTION: 'production',
    DEBUG: 'debug', 
    PROFILE: 'profile'
};

export const getBuildConfig = () => {
    const config = {
        target: "profile",
        isProduction: false,
        isDebug: false,
        isProfile: true,
        
        // Performance settings
        enablePerformanceTracking: true,
        enableDebugLogging: false,
        enableAssertions: false,
        enableResourceTracking: true,
        enableVerboseErrors: false,
        
        // Optimization flags
        minifyCode: false,
        treeShaking: true,
        stripDebugCode: false,
        inlineConstants: false,
        
        // Queue optimization settings
        queueOptimizations: {
            useObjectPooling: true,
            fastPathExecution: true,
            minimizeAllocations: false,
            enableDetailedStats: true,
            debounceDelay: 1,
            maxPoolSize: 20
        },
        
        // Performance targets (ms)
        performanceTargets: {
            queueOverhead: 10,
            maxFrameTime: 16.67, // 60 FPS
            gpuCommandSubmission: 2
        }
    };
    
    return config;
};

export const BUILD_FLAGS = {
    // Compile-time constants for conditional compilation
    __PRODUCTION__: false,
    __DEBUG__: false,
    __PROFILE__: true,
    __DEV__: true
};
                